import { BlockMock, DeepPartial } from '../core'
import { TokenBlock } from './token-block.model'

/**
 * Token Block Mock
 */
export function TokenBlockMock(options?: DeepPartial<TokenBlock>): TokenBlock {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'TOKEN_BLOCK_ID',

    block: BlockMock(options?.block),
    tokenId: options?.tokenId || 'TOKEN_ID',
    tokenAddress: options?.tokenAddress || 'TOKEN_ADDRESS',
    price: options?.price || '1',

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}
